.smallDashboard {
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: center;
	
	& .contentBox {
		display: flex;
		flex-direction: column;
		align-items: center;

		& .desc {
			display: flex;
			flex-direction: column;
			margin-top: 20px;
			max-width: 300px;
			justify-content: center;
			align-items: center;

			& .emoji {
				width: 200px;
				height: 200px;
				background-size: cover;
			
				&.welcome {
					background-image: url(https://www.iconinteractive.com/wp-content/uploads/2015/12/Exp_Icon.gif);
				}

				&.twoStep {
					background-position: 0 -33px;
    			height: 136px;
					background-image: url(http://finelinesmarketing.com/wp-content/uploads/2018/04/animation_2.gif);
				}
			}

			& .text {
				margin-top: 20px;
				text-align: center;
				font-size: 20px;
			}
		}
	}
}