.blackoutBlock {
	color: #e2e2e4;
	display: flex;
	flex-direction: column;
	padding: 20px;
	position: relative;
  background-image: linear-gradient(40deg, rgba(255,255,255,.09) 47%, rgba(255,255,255,.02));
	
	@media screen and (max-width: 900px) {
    flex: 1 !important;

    &:after {
    	display: none;
    }
  }
}

.blackoutBlock.keys {
	flex: 0.3;
	display: flex;
	min-width: 300px;
	overflow: auto;
	max-height: 100%;

	@media screen and (max-width: 900px) {
    max-height: none;
    overflow: auto;
  }
}

.blackoutBlock.products {
	flex: 0.3;
	display: flex;
	min-width: 300px;
	overflow: auto;
	max-height: 100%;

	@media screen and (max-width: 900px) {
    max-height: none;
    overflow: auto;
  }
}

.blackoutBlock.dashboard {
	flex: 0.4;
	display: flex;
	min-width: 300px;
	overflow: auto;
	max-height: 100%;

	@media screen and (max-width: 900px) {
    max-height: none;
    overflow: auto;
  }
}

.blackoutBlock.requisitions {
	display: flex;
	flex: 0.3;
}

.blackoutBlock.myproductinfo {
	display: flex;
	flex: 0.7;
	overflow: auto;
}











