.internalPage {
	width: 100%;
	height: 100%;
	position: fixed;
	background-image: linear-gradient(180deg,#231a37,#464d63),linear-gradient(rgba(0,0,0,.17),rgba(0,0,0,.17));	
	// background: #3A4169;
	display: flex;
	align-items: center;
	flex-direction: column;
	overflow: auto;
	animation: 1s ease pulse;

	> .container {
		display: flex;
		align-items: center;
		flex-direction: column;
		flex: 1;
		width: 100%;
		max-width: 1440px;

		& .contentChildren {
			width: 100%;
			flex: 1;
			flex-wrap: wrap;
			margin-bottom: 300px;
		}
	}

	.ieoMessage {
		display: flex;
		position: fixed;
		bottom: 0;
		background: white;
		width: 100%;
		align-items: center;
		flex-direction: column;

		> .container {
			display: flex;
			align-items: center;
			flex-direction: column;
			flex: 1;
			max-width: 800px;
			padding: 20px 0;
			width: 80%;
		}
	}
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  
  25% {
    transform: scale(1.115);
  }
  
  50% {
    transform: scale(1);
  }
}